/* 
* 
*
*/
import asyncComponent from './asyncComponent';
export default [{ 
    path: '/check', 
    name: '', 
    exact: true,
    component: asyncComponent(() => import("../container/check"),''),
  }, {
    path: '/check/evaluate', 
    name: '', 
    exact: true,
    component: asyncComponent(() => import("../container/check/evaluate"),''),
  }, {
    path: '/check/assetsShow', 
    name: '', 
    exact: true,
    component: asyncComponent(() => import("../container/check/assetsShow"),''),
  }, {
    path: '/check/parts/:id', 
    name: '', 
    exact: true,
    component: asyncComponent(() => import("../container/check/partsInfo"),''),
  },{
    path: '/check/detail/:userId/:orderFstate/:rrpairOrderGuid/:userType/:sessionId', 
    name: '', 
    component: asyncComponent(() => import("../container/check/detail"),''),
  },]


