import asyncComponent from './asyncComponent';

export default [{ 
  path: '/waitForRepair', 
  name: '', 
  exact: true,
  component: asyncComponent(() => import("../container/waitForRepair"), ''),
}, {
  path: '/waitForRepair/detail/:userId/:rrpairOrderGuid/:userType/:sessionId', 
  name: '', 
  component: asyncComponent(() => import("../container/waitForRepair/detail"), ''),
}, {
  path: '/waitForRepair/detail/:result', 
  name: '', 
  exact: true,
  component: asyncComponent(() => import("../container/waitForRepair/result"), ''),
}]


