import asyncComponent from './asyncComponent';

export default [{ 
  path: '/startToRepair/stepOne/:id', 
  name: '', 
  exact: true,
  component: asyncComponent(() => import("../container/startToRepair"), '1'),
}, {
  path: '/startToRepair/stepTwo/:id', 
  name: '', 
  exact: true,
  component: asyncComponent(() => import("../container/startToRepair/stepTwo"), '2'),
}, {
  path: '/startToRepair/stepThree/:id', 
  name: '', 
  exact: true,
  component: asyncComponent(() => import("../container/startToRepair/stepThree"), '3'),
}, {
  path: '/startToRepair/stepTwo/addParts/:id', 
  name: '', 
  exact: true,
  component: asyncComponent(() => import("../container/startToRepair/addParts"), ''),
}]


