const initialState = {
  assetsRecord: {
    assetsRecordGuid: '',
    assetsRecord: '', //
    equipmentName: '', // 
    fmodel: '', // 
    spec: '',// 
    useFstate: '',// 
    guaranteeFlag: '',// 
    files: [], // 
    faultDescribeText: '', //  
    faultDescribe: '', //  key
    deposit: '', // 
    rrpairPhone: '' // 
  },
  useFstate: '',
  urgentFlag: '',
  spare: '',
  faultDescribe: [],
  files: [],
  submitFiles:[],
  failCause: ''
};
const repairReducer = (state = initialState , action) => {
  switch (action.type) {
    case 'SET_REPAIR_MAPPER':
      return {
        ...action.repair
      }
    default:
      return state
  }
}
export default repairReducer;


